import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController } from 'ionic-angular';
import { ApiServiceProvider } from '../../providers/api-service/api-service';
import { Storage } from '@ionic/storage';


/**
 * Generated class for the ContestPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-contest',
  templateUrl: 'contest.html',
})
export class ContestPage {
  image: any;
  loading: any;
  deviceId: any;
  employeeId: any;
  employeeType: any;
  loginToken: any;
  allData: any;
  value: any='0';
  contestList: any;
  myInfiniteScroll:any;
  infiniteScrollCalled:boolean=false;
  hideInfiniteScroll:boolean=false;
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public toastCtrl: ToastController,private storage:Storage,
    private apiServices:ApiServiceProvider,public loadingCtrl: LoadingController,
    ) {
      this.storage.get('deviceId').then(data=>{
        this.deviceId=data;
        console.log('login deviceId== ',this.deviceId)
       });
       this.storage.get('employeeId').then(data=>{
        this.employeeId=data;
        console.log('login employeeId== ',this.employeeId);
        // this.getFaq();
       });
       
       this.storage.get('showOnBoard').then(data=>{
        this.employeeType=data;
        console.log('login employeeId== ',this.employeeId);
        // this.getFaq();
       });
       this.storage.get('login_token').then(data=>{
        this.loginToken=data;
        console.log('login employeeId== ',this.employeeId);
        this.getConList();
       });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ContestPage');
  }
  getConList(){
    this.commonLoader();
    let apiKey={
      "client_id":this.apiServices.clientId,
      "employee_id":this.employeeId,
      "device_id":this.deviceId,
      "app_version":this.apiServices.appVersion,
      "device":this.apiServices.device,
      "user_type":this.employeeType,
    };

console.log('home page api key==', apiKey);
// this.apiServices.faqDetails(apiKey).then((result) => { 
this.apiServices.getContestList(apiKey,this.loginToken).subscribe((result) => {   
  console.log('faqDetails == ',result);
  this.allData=result;
  this.loading.dismiss(); 
  if(this.allData.success==1){
    if(this.contestList==undefined){
      this.contestList = this.allData.data;
    }else{
      this.contestList= this.contestList.concat(this.allData.data);
      console.log('list==',this.allData.data );
    }
    if(this.infiniteScrollCalled==true){
      this.myInfiniteScroll.complete();
      this.infiniteScrollCalled=false;
    }
    console.log('==',this.contestList);
    this.hideInfiniteScroll=false;     
  } else {
    this.contestList=[];
    this.image=result.image;
    this.apiMessage(this.allData.message);
    // this.navCtrl.pop();
  }
});

  }

  apiMessage(message) {
    const toast = this.toastCtrl.create({
      message: message,
      duration: 3000
    });
    toast.present();
  }
  doInfinite(infiniteScroll) {
    console.log('Begin async operation');
  this.myInfiniteScroll=infiniteScroll;
    setTimeout(() => {
      this.infiniteScrollCalled=true;
      this.value=this.contestList.length;
      this.getConList();
      console.log('Async operation has ended');
      // this.myInfiniteScroll.complete();
    }, 500);
  }
  commonLoader() {
    this.loading = this.loadingCtrl.create({
      enableBackdropDismiss: true,
      spinner: 'ios-small',
      content: 'Loading data...'
    });
    this.loading.present();
  }
  gotodetail(data){
    this.navCtrl.push('ContestdetailPage',{'data':data})
  }
}



